import { Link } from "gatsby";
import React, { useState } from "react";
import { useStaticQuery, graphql } from "gatsby";
import "./product.css";
import { products } from "../../mockData";
import CallToAction from "../master/cta";

const ProductDetails = ({ product }) => {
  const [selectedProduct, setSelectedProduct] = useState();
  const data = useStaticQuery(graphql`
    query MyQuery {
      allFile {
        edges {
          node {
            publicURL
            sourceInstanceName
            relativePath
          }
        }
      }
    }
  `);

  const imgUrls = data.allFile.edges;

  let productImg = imgUrls.find((file) =>
    file.node.publicURL.endsWith(
      selectedProduct ? selectedProduct?.productImg : product?.productImg
    )
  );
  let brocherUrl = imgUrls.find((file) =>
    file.node.publicURL.endsWith(product?.brochure)
  );
  const handleShadeRedirect = (shade) => {
    const pro = products.find(
      (el) => el.categoryId === product.categoryId && el.id === shade.id
    );
    console.log("shade", pro);
    setSelectedProduct(pro);
    // navigate(`/products/${pro?.id}`);
  };

  return (
    <div className="productdetail-section home-content">
      <div className="header-title">
        <div className="container">
          <div className="header-title-text">
            <h2>
              EXPLORE <span>OUR TILE RANGE</span>
            </h2>
            <ul>
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="/products">/ Products</Link>
              </li>
              <li>
                / {selectedProduct ? selectedProduct?.name : product?.name}
              </li>
            </ul>
          </div>
        </div>
      </div>

      <div className="productdetail-wrapper">
        <div className="productdetail-left">
          <div className="mainImgContainer">
            <img src={productImg?.node?.publicURL} alt="" />
          </div>
          <div className="otherShadesTypeImg">
            {product?.shadeImgs?.map((shade) => (
              <img
                onClick={() => handleShadeRedirect(shade)}
                src={
                  imgUrls.find((el) =>
                    el.node.publicURL.endsWith(shade.shadeImg)
                  )?.node.publicURL
                }
                alt=""
              />
            ))}
          </div>
        </div>

        <div className="productdetail-right">
          <div className="product-right-wrapper">
            <div className="productdetail-title">
              <h5>{selectedProduct ? selectedProduct.id : product?.id}</h5>
            </div>
            <div className="productdetail-desc">
              <div className="productdeatails-subtitle">
                <h6>Series</h6>
                <span>:</span>
                <span>
                  {selectedProduct ? selectedProduct.series : product?.series}
                </span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Code</h6>
                <span>:</span>
                <span>
                  {selectedProduct ? selectedProduct.code : product?.code}
                </span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Description</h6>
                <span>:</span>
                <span>
                  {selectedProduct
                    ? selectedProduct.description
                    : product?.description}
                </span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Design Faces</h6>
                <span>:</span>
                <span>
                  {" "}
                  {selectedProduct
                    ? selectedProduct.designFaces
                    : product?.designFaces}
                </span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Slip Rating</h6>
                <span>:</span>
                <span>
                  {selectedProduct
                    ? selectedProduct.slipRating
                    : product?.slipRating}
                </span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Finish</h6>
                <span>:</span>
                <span>
                  {selectedProduct ? selectedProduct.finish : product?.finish}
                </span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Edge</h6>
                <span>:</span>
                <span>
                  {selectedProduct ? selectedProduct.edge : product?.edge}
                </span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Area</h6>
                <span>:</span>
                <span>
                  {selectedProduct ? selectedProduct.area : product?.area}
                </span>
              </div>
            </div>

            <div className="productdetail-btn">
              <Link className="enquireBtn" to="/contactUs">
                Enquire Now
              </Link>
              {product?.brochure !== "" ? (
                <Link
                  className="pdfBtn"
                  href={brocherUrl?.node?.publicURL}
                  download={true}
                >
                  Download Brochure <PdfDownIcon />
                </Link>
              ) : (
                ""
              )}
            </div>
          </div>
        </div>
      </div>

      <CallToAction />
      <div className="product-look-section home-contact">
        <div className="home-contact-grid">
          <h2>
            Have a <span> Look</span>
          </h2>
        </div>
      </div>
    </div>
  );
};

export default ProductDetails;

